@use 'base/mixins' as m;
@use 'base/variables' as v;

.acciones {
    display: flex;
    justify-content: space-evenly;
    .info {
        width: 40%;
        padding: 1rem 4rem 4rem 4rem;
    }
    .membresia {
        width: 40%;
        padding: 1rem 4rem 4rem 4rem;
    }
    .crear-cuenta {
        width: 40%;
        padding: 1rem 4rem 4rem 4rem;
    }
    p {
        font-family: v.$fuente_principal;
        margin: 0;
        font-size: 1.6rem;
    }
    a {
        font-size: 1.5rem;
        @include m.boton(v.$amarillo_pastel, v.$azul);
        border-radius: 1rem;
    }
}   