@use 'variables' as v;
@use 'base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
    scroll-behavior: smooth;
    
}
body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$negro;
    color: v.$blanco;

    @include m.tablet {
        overflow: auto;
    }
    /*Webkit(Chrome, Android browser, Safari, Edge...)*/
    &::-webkit-scrollbar { // Esto nos permite seleccionar la barra de scroll como parametro y al aplicarle un display none ya no se ve pero sigue funcionando
        display: none;
    }
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
.contenedor-sm {
    width: 70%;
    max-width: 1000px;
    margin: 0 auto;
}

.centrado-vertical {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

.bg-simple {
    height: 20rem;
    background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(../img/7.jpg); // La mandamos llamar desde la posición del app.scss
    backdrop-filter: blur(2px) ;
    background-size: cover; // Hace que cubra todo el espacio la imagen
    background-position: center center; // La coloca exactamente en el medio
    @include m.tablet {
        height: auto;
    }
}
.app {
    @include m.tablet {
        overflow: auto;
    }
    &::-webkit-scrollbar { // Esto nos permite seleccionar la barra de scroll como parametro y al aplicarle un display none ya no se ve pero sigue funcionando
        display: none;
    }
}