@use 'base/mixins' as m;
@use 'base/variables' as v;

.titulo-planes {
    font-size: 6rem;    
}
.contenido-membresia {
    
    @include m.tablet {
        @include m.grid(2, 4rem);
    }
    .anual,
    .mensual,
    .semanal,
    .diaria {
        display: grid;
        justify-content: center;
        align-items: center;
        .comprar-membresia {
            @include m.boton(v.$azul, v.$blanco);
            border-radius: 1rem;
        }
        .imagen {
            margin: 0 auto;
        }
    }
    h2 {
        font-size: 2.5rem;
        margin-bottom: 1rem;
    }
    img {
        height: 15rem;
        margin: 0 auto;
    }
}