@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-app {
    margin-top: 5rem;
    @include m.tablet {
        margin-top: 0;
    }

    .app {
        @include m.tablet {
            overflow: auto;
        }
        /*Webkit(Chrome, Android browser, Safari, Edge...)*/
        &::-webkit-scrollbar { // Esto nos permite seleccionar la barra de scroll como parametro y al aplicarle un display none ya no se ve pero sigue funcionando
            display: none;
        }
        // &:hover::-webkit-scrollbar {
        //     display: initial;
        // }
        // &::-webkit-scrollbar-thumb {
        //     background-color: v.$negro;
        // }
    }
}

.titulo-index {
    h1 {
        font-size: 3rem;
        @include m.tablet {
            font-size: 7rem;
        }
    }

    img {
        display: block;
        margin: 0 auto;
        max-width: 80%;
    }
}

.descripcion-index {
    h3 {
        font-size: 1.8rem;
        margin-bottom: 1rem;
        @include m.tablet {
            font-size: 3rem;
        }
    }
}