/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  /*Webkit(Chrome, Android browser, Safari, Edge...)*/
}
@media (min-width: 768px) {
  body {
    overflow: auto;
  }
}
body::-webkit-scrollbar {
  display: none;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  width: 70%;
  max-width: 1000px;
  margin: 0 auto;
}

.centrado-vertical {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.bg-simple {
  height: 20rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url(../img/7.jpg);
  backdrop-filter: blur(2px);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .bg-simple {
    height: auto;
  }
}

@media (min-width: 768px) {
  .app {
    overflow: auto;
  }
}
.app::-webkit-scrollbar {
  display: none;
}

h1, h2, h3 {
  margin: 0 0 2.5rem 0;
  font-weight: 700;
  color: #0da6f3;
  text-align: center;
}

h1 {
  font-family: "Montserrat Alternates", sans-serif;
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-family: "Rubik", sans-serif;
  font-size: 3rem;
  text-align: center;
  font-weight: 400;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.contenedor-app {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenedor-app {
    margin-top: 0;
  }
}
.contenedor-app .app {
  /*Webkit(Chrome, Android browser, Safari, Edge...)*/
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: auto;
  }
}
.contenedor-app .app::-webkit-scrollbar {
  display: none;
}

.titulo-index h1 {
  font-size: 3rem;
}
@media (min-width: 768px) {
  .titulo-index h1 {
    font-size: 7rem;
  }
}
.titulo-index img {
  display: block;
  margin: 0 auto;
  max-width: 80%;
}

.descripcion-index h3 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .descripcion-index h3 {
    font-size: 3rem;
  }
}

.titulo-planes {
  font-size: 6rem;
}

@media (min-width: 768px) {
  .contenido-membresia {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenido-membresia .anual,
.contenido-membresia .mensual,
.contenido-membresia .semanal,
.contenido-membresia .diaria {
  display: grid;
  justify-content: center;
  align-items: center;
}
.contenido-membresia .anual .comprar-membresia,
.contenido-membresia .mensual .comprar-membresia,
.contenido-membresia .semanal .comprar-membresia,
.contenido-membresia .diaria .comprar-membresia {
  background-color: #0da6f3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 1rem;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 3rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .contenido-membresia .anual .comprar-membresia,
  .contenido-membresia .mensual .comprar-membresia,
  .contenido-membresia .semanal .comprar-membresia,
  .contenido-membresia .diaria .comprar-membresia {
    width: auto;
  }
}
.contenido-membresia .anual .comprar-membresia:hover,
.contenido-membresia .mensual .comprar-membresia:hover,
.contenido-membresia .semanal .comprar-membresia:hover,
.contenido-membresia .diaria .comprar-membresia:hover {
  background-color: #076493;
  cursor: pointer;
}
.contenido-membresia .anual .imagen,
.contenido-membresia .mensual .imagen,
.contenido-membresia .semanal .imagen,
.contenido-membresia .diaria .imagen {
  margin: 0 auto;
}
.contenido-membresia h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
.contenido-membresia img {
  height: 15rem;
  margin: 0 auto;
}

.navegacion {
  height: 10rem;
  margin-bottom: 2rem;
  display: flex;
  justify-content: right;
}
.navegacion svg {
  margin: 2rem 3rem;
}

.acciones {
  display: flex;
  justify-content: space-evenly;
}
.acciones .info {
  width: 40%;
  padding: 1rem 4rem 4rem 4rem;
}
.acciones .membresia {
  width: 40%;
  padding: 1rem 4rem 4rem 4rem;
}
.acciones .crear-cuenta {
  width: 40%;
  padding: 1rem 4rem 4rem 4rem;
}
.acciones p {
  font-family: "Poppins", sans-serif;
  margin: 0;
  font-size: 1.6rem;
}
.acciones a {
  font-size: 1.5rem;
  background-color: #ffea70;
  padding: 1.5rem 4rem;
  color: #0da6f3;
  margin-top: 1rem;
  font-size: 1.8rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 3rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .acciones a {
    width: auto;
  }
}
.acciones a:hover {
  background-color: #ffdb0a;
  cursor: pointer;
}

.flecha {
  cursor: pointer;
  text-align: center;
}
.flecha svg:hover {
  transition: 300ms ease-in-out;
  width: 8rem;
  height: auto;
}

.ocultar {
  display: none;
}