@use 'variables' as v;

h1, h2, h3 {
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 700;
    color: v.$azul;
    text-align: center;
}

h1 {
    font-family: v.$fuente_titulos;
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-family: v.$fuente_subtitulos;
    font-size: 3rem;
    text-align: center;
    font-weight: 400;
}
p {
    color: v.$blanco;
    font-size: 2rem;
}
